// returns either of 'hi_IN', 'ml_IN'

function getLanguagePerStateName(stateName){
    
    // if stateName is 'Kerala' => language is malayalam else hindi
    if(stateName === 'കേരളം')
        return 'ml_IN';
    else
        return 'hi_IN';
}

module.exports = getLanguagePerStateName;


