// RETURN _ID FOR THE PASSED STATENAME
function returnStateId(stateName){
    for(key in stateId){
        if(stateId[key].display === stateName){
            return String(stateId[key]["_id"]);
        }
    }
}

// RETURN _ID FOR THE PASSED CITYNAME
function returnCityId(cityName){
    let index = -1;
    for(key in cityId){
        index = cityId[key].findIndex(obj => 
            obj.display === cityName
        );
        if(index > -1){
            return String(cityId[key][index]["_id"]);
        }
    }
}

