import hamburger from "../assets/hamburger.png";

export default function PostsHamburger(props){
    return (
        <div class="PostsCalendarAndHamburger">
            <div class="calendarAndHamburger-child3">
                <img class="hamburger-image" src={ hamburger } onClick={()=>props.displayStateChoosing()}/>
            </div>
        </div>
    );
}


