import preact from 'preact';
import Circle from './circle';
const trendingTopics = require('../Config').trendingTopics;

function Header(props){
    return (
        <div class="header-container hamburger-header-container">
            <Circle/>
            <div class="hamburger-cross-button-container">
                <h1 class="hamburger-cross-button" onClick={()=>props.toggleHamburger()}>x</h1>
            </div>
        </div>
    )
}

function RestContent(props){

    let stateName = localStorage.getItem('stateName');
    let cityName = localStorage.getItem('cityName');
    let lang = localStorage.getItem('lang');
   
    //  push Home, About US, Contact Us
    let jsxOptions = [];

    jsxOptions.push(
        <h3 class="hamburger-options" onClick={()=> props.toggleHamburger('Home') }>Home</h3>,
        <h3 class="hamburger-options" onClick={()=>{ props.toggleHamburger(); window.location = 'https://circle.page/about?utm_source=amp&utm_medium=nav&utm_campaign=amp' }}>About Us</h3>,
        <h3 class="hamburger-options" onClick={()=>{ props.toggleHamburger(); window.location = 'https://circle.page/contact#?utm_source=amp&utm_medium=nav&utm_campaign=amp' }}>Contact Us</h3>,
        );
    
    let newsWord = lang === "hi_IN" ? ' न्यूज़' : ' വാർത്തകൾ';

    // push city if present in localStorage
    if( cityName !== null )
        jsxOptions.push(<h3 class="hamburger-options" onClick={()=>props.toggleHamburger('City')}>{cityName + newsWord}</h3>);
    
    // push state if present in localStorage
    if( stateName !== null )
        jsxOptions.push(<h3 class="hamburger-options" onClick={()=>props.toggleHamburger('Home')}>{stateName + newsWord}</h3>);
    
    // push topics
    trendingTopics[lang].forEach(item=>{
        jsxOptions.push(<h3 class="hamburger-options" onClick={()=>props.toggleHamburger('default',item.value)}>{item.topic}</h3>);
    })

    return (
        <div class="hamburger-restContent-container">
            { jsxOptions }
        </div>
    );
}

export default class Hamburger extends preact.Component{
    render(){
        return (
            <div class="hamburger-container">
                <Header toggleHamburger={this.props.toggleHamburger}/>
                <RestContent toggleHamburger={this.props.toggleHamburger}/>
            </div>
        )
    }
}


