import { Fragment } from "preact";

import saharanpur from "../../assets/poster/saharanpur.gif"
import udaipur from "../../assets/poster/udaipur.gif";
import hrElections from "../../assets/poster/hrElections.gif";

let clevertap = require("../../Apis").clevertap;
let hr = require('../../Config').hr;

function CustomisedPoster(props){
    
    function hrElectionsClick(){
        clevertap({
            eventName: 'WEB_HAR_ELECTION',
            identity: localStorage.getItem('fingerprint'),
            eventData: {
                timePageOpened: props.state.timePageOpened,
                eventTime: new Date().getTime(),
                postNumber: props.state.postNumber,
                origin: props.state.origin,
                language: props.state.lang,
                shehar: props.state.city,
                sheharId: props.state.cityId,
            },
        });
        window.open("https://play.google.com/store/apps/details?id=news.circle.circle&referrer=utm_source%3Dhar_election%26utm_medium%3Dapp");
    }

    if(props.hr)
        return (
            <Fragment>
                <div class="posts-space"/>
                <div class="customisedPoster-container">
                    <img onClick = { ()=> hrElectionsClick() }
                        class="customisedPoster-item" src={ hrElections } alt="haryana elections"></img>
                </div>
            </Fragment>
            );
    else{
        if(props.city === 'उदयपुर')
            return (
            <Fragment>
                <div class="posts-space"/>
                <div class="customisedPoster-container">
                    <img onClick = { ()=> window.open("https://chat.whatsapp.com/GqXJ6opoJu07icsPp6W1EA") }
                        class="customisedPoster-item" src={ udaipur } alt="udaipur"></img>
                </div>
            </Fragment>
            );
        else if(props.city === 'सहारनपुर')
            return (
            <Fragment>
                <div class="posts-space"/>
                <div class="customisedPoster-container">
                    <img onClick = { ()=> window.open("https://chat.whatsapp.com/HMCawN7bFHOJ1PGqhLbJZl") }
                        class="customisedPoster-item" src={ saharanpur } alt="saharanpur"></img>
                </div>
            </Fragment>
            );
        else
            return null;
    }
}

export default CustomisedPoster;


