import preact from "preact";
import Loading from "./loading";
import PostsHamburger from "./postsHamburger";
import ShowStateSelection from "./showStateSelection";
import img from "../assets/circle-logo.svg";

import CustomisedInstallDiv from "./customised/customisedInstallDiv";
import CustomisedPoster from "./customised/customisedPoster";

const clearIntervalId = require("../functional-Javascript/clearIntervalId");
const hr = require("../Config").hr;

let clevertap = require("../Apis").clevertap;
let config = require("../Config");

// FOR HLS VIDEO SUPPORT
let Hls = require("hls.js");

let fetchPost = require('../fromAmp/posts');

function Circle(props){
    return(
        <div class="circle-container" onClick={()=>props.setHomePage()}>
            <div class="circle-image-container">
                <img class="circle-image MarginAuto" alt="image" src={img}/>
            </div>
            <div class="circle-title-container">
                <h2 class="circle-title">circle</h2>  
            </div>        
        </div>
    );
}

class Posts extends preact.Component{
    constructor(props){
        super();
        this.state = {
            // 
            loading: true,
            error: false,

            displayState: false,

            postNumber: props.postNumber,

            type: "", // image/video
            url: "", // corresponding to the type above
            date: "",
            city: "",
            headline: "",
            reporter: "",
            news: "",
            
            // array of objects each containing keys - openUrl, text
            tags: [],
            
            // array of objects each containing keys - openUrl, thumbnailUrl, title
            topPosts: [],
            relatedPosts: [],
            installMsg1: "", installMsg2: "",
            message1: "", message11: "", message2: "", message3: "",
            displayMessage: "",
            displayMessageNumber: 0,
            lang: "",
            timePageOpened: "",
            reporterId: "",
            reporterName: "",
            
            // RECORDING EVENT FIRED STATUSES
            eventFired_WEB_VIDEO_PLAYED: true,
        }
        sessionStorage.setItem('intervalFired',true);
        this.videoRef = preact.createRef();
    }

    componentDidMount(){
        /* PERFORM FETCH REQUESTS AND UPDATE THE DATA ON THE PAGE*/
        this.getPost(this.state.postNumber);

        // ADD EVENT LISTENER FOR BROWSER EXIT
        this.eventListener_add_beforeUnLoad();

        // SET UP THE SET-INTERVAL FUNCTION TO TIMELY CHANGE THE VALUE OF THE DISPLAY MESSAGE
        this.changeMessage(this.state.displayMessageNumber);
    }

    componentWillUnmount(){

        // REMOVE EVENT LISTENER FOR BROWSER EXIT
        this.eventListener_remove_beforeUnLoad();
    }

    componentDidUpdate(prevProp,prevState){
        // console.log('prevState',prevState,'\nCurrentState',this.state);
        // console.log('component did update');

        if(this.state.type !== 'image'){
            // START PLAYING VIDEO WHEN THE NEW POST HAS BEEN LOADED 
            // as prevState would have loading as true and current as notLoading
            if( prevState.loading && !this.state.loading)
                this.playVideo(this.state.type);

            // CROSS CLICKED ON STATE SELECTION THEREFORE THE VIDEO MUST CONTINUE 
            if( prevState.displayState && !this.state.displayState)
                this.playVideo(this.state.type);
        }

        // SETUP TIMER WHEN THE POST IS OF TYPE video OR m3u8
        if(prevState.eventFired_WEB_VIDEO_PLAYED && !this.state.eventFired_WEB_VIDEO_PLAYED && this.state.type !== 'image'){
            // console.log('timer setup karna hai bro');
            this.setupTimerForCleverTapEvent(this.state.type);
        }

        if(this.state.loading){
            // console.log('requesting for postNumber',this.state.postNumber);
            this.getPost(this.state.postNumber);
        }

        if( prevProp.postNumber !== this.props.postNumber ){
            // console.log('not matching');
            // console.log('prev postNumber',prevProp.postNumber,'\nCurrent PostNumber',this.props.postNumber);
            this.setState({ postNumber: this.props.postNumber, loading: true });
        }
    }

    setupTimerForCleverTapEvent(type){

        let intervalState = {
                timePageOpened: this.state.timePageOpened,
                eventTime: new Date().getTime(),
                postNumber: this.state.postNumber,
                origin: 'post',
                language: this.state.lang,
                city: this.state.city,
                cityId: this.state.city ? config.getId(this.state.city,false) : "",
                reportedId: this.state.reporterId,
                reporterName: this.state.reporterName,
                browserClose: true,
                type: this.state.type,
            }
            
            let intervalVideoTime = null;
            let videoElement = null;
            let intervalDuration = null;

            if(type !== "image"){
                videoElement = this.videoRef.current;
                
                if(videoElement){
                intervalDuration = this.videoRef.current.duration;
                intervalVideoTime = this.videoRef.current.currentTime;

                sessionStorage.setItem('intervalDuration',intervalDuration);
                sessionStorage.setItem('intervalVideoTime',intervalVideoTime);
                sessionStorage.setItem('intervalState',JSON.stringify(intervalState));
                }
            }


        let intervalId = setInterval(()=>{
            let intervalVideoTime = null;
            let videoElement = null;
            let intervalDuration = null;

            if(type !== "image"){
                videoElement = this.videoRef.current;
                
                if(videoElement){
                intervalDuration = this.videoRef.current.duration;
                intervalVideoTime = this.videoRef.current.currentTime;
                }
            }

            let id = sessionStorage.getItem('intervalId');
            
            // id not present => 1st time so setup timer
            if(!id){
                sessionStorage.setItem('intervalId',intervalId);
                sessionStorage.setItem('intervalDuration',intervalDuration);
                sessionStorage.setItem('intervalVideoTime',intervalVideoTime);
                sessionStorage.setItem('intervalState',JSON.stringify(intervalState));
            }
            
            // we have the intervalId process as required
            else{
                // type is video or m3u8
                if(this.state.type === 'm3u8' || this.state.type === 'video'){
                    if(videoElement){
                        intervalVideoTime = videoElement.currentTime;
                        intervalDuration = videoElement.duration;
                        
                        // end of video. So stop the timer
                        if(intervalVideoTime === intervalDuration)
                            clearInterval(Number(sessionStorage.getItem('intervalId')));

                        sessionStorage.setItem('intervalDuration',intervalDuration);
                        sessionStorage.setItem('intervalVideoTime',intervalVideoTime);
                    }
                }
                // if type is image clear interval and corresponding data
                else
                    clearIntervalId();

            }
        }, 3000);
    }

    // START PLAYING VIDEO WHEN THE VIDEO TAG IS SET IN THE DOM
    playVideo(type){
        // console.log('working kid bro');
        if(type === 'm3u8')
            if (Hls.isSupported()) {
                // console.log('wroking on hls video');
                let hls = new Hls();
                hls.attachMedia(this.videoRef.current);
                hls.loadSource(this.state.url);
                hls.on(Hls.Events.MANIFEST_PARSED, function(){
                    this.videoRef.current.play();
                });
            }
        let resumeTime = sessionStorage.getItem('temp-resumeTime');
        if(resumeTime){
            // console.log('resuming video');
            this.videoRef.current.play();
            this.videoRef.current.currentTime = Number(resumeTime);
            sessionStorage.removeItem('temp-resumeTime');
        }
    }

    /* FETCH POSTS PAGE FOR THE PASSED POST NUMBER AS AN ARGUMENT */
    getPost(postNumber){
        
        // GET POST REQUEST MADE FROM POSTS.JS IN FROMAMP
        let response = Promise.resolve(fetchPost(postNumber));

        response.then(response=>{
            this.setState({
                date: response.date,
                city: response.city,
                headline: response.headline,
                reporter: response.reporter,
                news: response.news,
                loading: false,
                topPosts: response.topPosts,
                relatedPosts: response.relatedPosts,
                type: response.type,
                url: response.url,
                message1: response.message1, message11: response.message11,
                message2: response.message2,
                message3: response.message3,
                installMsg1: response.installMsg1,
                installMsg2: response.installMsg2,
                lang: response.lang,
                timePageOpened: new Date().getTime(),
                reporterId: response.reportedId,
                reporterName: response.reporterName,

                // RECORDING EVENTS due to issues
                eventFired_WEB_VIDEO_PLAYED: response.type === 'image' ? true: false,
            });
            let intervalFired = response.type === 'image' ? true : false;
            sessionStorage.setItem('intervalFired',intervalFired);
            
            // CLEAR OLD sessionStorage -> intervalId, intervalState and intervalVideoTime
            clearIntervalId();
        });
    }

    changeMessage(){
        setInterval((index = this.state.displayMessageNumber)=>{
            let arr = [];
            // console.log('the value are',this.state.message1,this.state.message2,this.state.message2, this.state.message3);
            if(this.state.message1)
                arr.push(this.state.message1);
            if(this.state.message11)
                arr.push(this.state.message11);
            if(this.state.message2)
                arr.push(this.state.message2);
            if(this.state.message3)
                arr.push(this.state.message3);
            if(this.state.installMsg1){
                if(index === arr.length)
                    index = 0;
                this.setState({ displayMessage: arr[index], displayMessageNumber: index + 1});
            }
        },3000);
    }

    eventListener_beforeUnload(){
        if(sessionStorage.getItem('intervalState')){
            let type = JSON.parse(sessionStorage.getItem('intervalState')).type;
            if(type !== 'image'){
                let fired = sessionStorage.getItem('intervalFired');
                if(fired == "false"){
                    let videoDuration = Number(sessionStorage.getItem('intervalDuration'));
                    let videoTime = Number(sessionStorage.getItem('intervalVideoTime'));
                    let videoCompleteStatus = false;
                    if(videoDuration === videoTime)
                        videoCompleteStatus = true;
                    sessionStorage.setItem('intervalFired',true);
                    let stateObject  = JSON.parse(sessionStorage.getItem('intervalState'));
                    let clevertapObject = {
                        eventName: "WEB_VIDEO_PLAYED",
                        identity: localStorage.getItem("fingerprint"),
                        eventData: {
                            timePageOpened: stateObject.timePageOpened,
                            eventTime: new Date().getTime(),
                            postNumber: stateObject.postNumber,
                            origin: 'post',
                            language: stateObject.lang,
                            shehar: stateObject.city,
                            sheharId: stateObject.cityId,
                            videoDuration,
                            videoTime,
                            reportedId: stateObject.reporterId,
                            reporterName: stateObject.reporterName,
                            browserClose: false,
                            videoCompleteStatus,
                            videoPercentage: (videoTime * 100) / videoDuration,
                        }
                    };
                    clevertap(clevertapObject);
                }
                clearIntervalId();
            }
        }
        return "do you want to quit";
    }
    eventListener_add_beforeUnLoad(){
        window.addEventListener("beforeunload", this.eventListener_beforeUnload);
    }
    eventListener_remove_beforeUnLoad(){
        window.removeEventListener("beforeunload", this.eventListener_beforeUnload);
    }

    // show state selection
    setStateChoosing(){
        
        // execute event in case video is played
        this.clevertapEvent_WEB_VIDEO_PLAYED();
        
        // set time for video in session storage to provide resume support if needed
        {   let resumeTime = this.videoRef.current.currentTime;
            sessionStorage.setItem('temp-resumeTime',resumeTime);
        }

        this.setState({ displayState: true });
    }

    openNewPost(url, type){

        this.clevertapEvent_WEB_VIDEO_PLAYED();
        
        // CLEVERTAP EVENT = WEB_SUGGESTION_CLICK
        clevertap({
            eventName: 'WEB_SUGGESTION_CLICK',
            identity: localStorage.getItem('fingerprint'),
            eventData: {
                timePageOpened: this.state.timePageOpened,
                eventTime: new Date().getTime(),
                postNumber: this.state.postNumber,
                origin: 'post',
                language: this.state.lang,
                shehar: this.state.city,
                sheharId: this.state.city ? config.getId(this.state.city,false) : "",
                type,
                newPostNumber: url.slice(6),
            },
        });

        this.props.changePost(url);
    }

    // turn off state selection menu
    setOffStateChoosing(close = true){
        if(close)
            this.setState( { displayState: false });
        else
            // show the stateMenu
            this.props.setSheharPage();
    }

    goToHomePage(){
        
        // execute event in case video is played
        this.clevertapEvent_WEB_VIDEO_PLAYED();

        // CLEVERTAP EVENT = WEB_HOME_CLICK
        clevertap({
            eventName: 'WEB_HOME_CLICKED',
            identity: localStorage.getItem('fingerprint'),
            eventData: {
                timePageOpened: this.state.timePageOpened,
                eventTime: new Date().getTime(),
                postNumber: this.state.postNumber,
                origin: 'post',
                language: this.state.lang,
                shehar: this.state.city,
                sheharId: this.state.city ? config.getId(this.state.city,false) : "",
            }
        });
        this.props.setHomePage();
    }

    openInstallPage(){

        // execute event in case video is played
        this.clevertapEvent_WEB_VIDEO_PLAYED();

        // CLEVERTAP EVENT = WEB_INSTALL_NOW
        clevertap({
            eventName: "WEB_INSTALL_NOW",
            identity: localStorage.getItem("fingerprint"),
            eventData: {
                timePageOpened: this.state.timePageOpened,
                eventTime: new Date().getTime(),
                postNumber: this.state.postNumber,
                origin: 'post',
                language: this.state.lang,
                shehar: this.state.city,
                sheharId: this.state.city ? config.getId(this.state.city,false) : "",
            }
        })

        window.open("https://play.google.com/store/apps/details?id=news.circle.circle&referrer=utm_source%3Damp_k%26utm_medium%3Dbutton");
    }

    // CLEVERTAP EVENT = WEB_VIDEO_PLAYED
    clevertapEvent_WEB_VIDEO_PLAYED(){
        if(this.state.type !== 'image'){
            if(this.state.eventFired_WEB_VIDEO_PLAYED === false){
                let videoDuration = Number(sessionStorage.getItem('intervalDuration'));
                let videoTime = Number(sessionStorage.getItem('intervalVideoTime'));
                let videoCompleteStatus = false;
                if(videoDuration === videoTime)
                    videoCompleteStatus = true;
                this.setState({eventFired_WEB_VIDEO_PLAYED: true});
                sessionStorage.setItem('intervalFired',true);
                let stateObject  = JSON.parse(sessionStorage.getItem('intervalState'));
                let clevertapObject = {
                    eventName: "WEB_VIDEO_PLAYED",
                    identity: localStorage.getItem("fingerprint"),
                    eventData: {
                        timePageOpened: stateObject.timePageOpened,
                        eventTime: new Date().getTime(),
                        postNumber: stateObject.postNumber,
                        origin: 'post',
                        language: stateObject.lang,
                        shehar: stateObject.city,
                        sheharId: stateObject.cityId,
                        videoDuration,
                        videoTime,
                        reportedId: stateObject.reporterId,
                        reporterName: stateObject.reporterName,
                        browserClose: false,
                        videoCompleteStatus,
                        videoPercentage: (videoTime * 100) / videoDuration,
                    }
                };
                clevertap(clevertapObject);
            }
            clearIntervalId();
        }
    }    

    render(){

        // console.log('rendering');

        if(this.state.displayState)
            return <ShowStateSelection onClose={ (val) => this.setOffStateChoosing(val) }/>

        // console.log('render');
        if(this.state.error)
            return <div>Please Reload the Page... Unable to fetch data</div>;

        if(this.state.loading)
            return <Loading/>;

        /* LEFT FOR THE TIME BEING */
        function generateTags(array){
            let returnArray = [];
            array.forEach(item=>{
                returnArray.push(
                    <div class="posts-tag-item" onClick={()=>{;}}>{item.text}</div>
                );
            });
            return returnArray;
        }

        function generatePosts(array, type){
            let returnArray = [];
            array.forEach(item=>{
                returnArray.push(
                    <div class="flexC padding5" onClick={() => this.openNewPost(item.openUrl, type)}>
                        <div class="posts-otherPosts-post-image-container">
                            <div class="flexC">
                                <img class="posts-otherPosts-image" src={ item.thumbnailUrl } alt="thumbnail"></img>
                            </div>
                        </div>
                        <div class="posts-otherPosts-post-title-container">
                            <h2 class="posts-otherPosts-post-title">
                                { item.title }
                            </h2>
                        </div>
                        <br/>
                    </div>
                );
            });
            return returnArray;
        }

        let dateAndCity = <div class="posts-dateAndCity margin0">{ this.state.date }  |  {this.state.city}</div>;
        let headline = <div class="posts-news-title margin0">{ this.state.headline }</div>
        let reporter = <div class="posts-news-reporter">{ this.state.reporter }</div>
        
        function imageVideoContainer(type){
            switch(type){
                case "image" : return (
                                    <div class="margin10v fixedSize01">
                                        <img class="posts-imageVideo" src={ this.state.url }></img>
                                    </div>
                );

                case "video" : return (
                                    <div class="margin10v fixedSize01">
                                        <video class="posts-imageVideo" ref={ this.videoRef } controls autoplay>
                                            <source src={ this.state.url } type="video/mp4"/>
                                            HTML5 is not supported by the browser
                                        </video>
                                    </div>
                );
                case "m3u8" : return (
                                    <div class="margin10v fixedSize01">
                                        <video class="posts-imageVideo" controls id="newsVid" ref={ this.videoRef } autoplay>
                                            HTML5 is not supported by the browser
                                        </video>
                                    </div>
                );
                default : return null;
            }
        }

        let shareButtons = <div class="posts-shareButton-container">
                <div onClick={ ()=> window.open("https://www.facebook.com/dialog/share?app_id=1950057805033895&display=popup&href=https://circle.page/post/" + this.state.postNumber + "?route=android&utm_source=amp&utm_medium=wa&utm_campaign=amp") } 
                    class="posts-shareButton posts-fb"><div class="MarginAuto">facebook</div></div>
                <div onClick={ () => window.open("whatsapp://send?text=" + this.state.headline + " https://circle.page/post/" + this.state.postNumber + "?route=android&utm_source=amp&utm_medium=wa&utm_campaign=amp") } 
                    class="posts-shareButton posts-whatsapp"><div class="MarginAuto">WhatsApp</div></div>
            </div>;
        let news = <p class="posts-news">{ this.state.news }</p>;
        
        // LEFT FOR THE TIME BEING
        // let tags = <div class="posts-tags-container">
        //         { generateTags(this.state.tags) }
        //     </div>

        let main = <div class="posts-container">
                { dateAndCity }
                { headline }
                { reporter }
                { imageVideoContainer.call(this,this.state.type) }
                { shareButtons }
                { news }
                {/* { tags } */}
            </div>;

        let topPosts = (this.state.topPosts.length ? 1 : null) && <div class="posts-container">
                <div class="posts-otherPosts-container-title">
                    Top Posts
                </div>
                <div class="posts-otherPosts-container">
                    { generatePosts.call(this,this.state.topPosts,"topPosts") }
                </div>
            </div>;
        
        let relatedPosts = (this.state.relatedPosts.length ? 1 : null) && <div class="posts-container">
                <div class="posts-otherPosts-container-title">
                    Related Posts
                </div>
                <div class="posts-otherPosts-container">
                    { generatePosts.call(this,this.state.relatedPosts,"relatedPosts") }
                </div>    
        </div>

        // let installDiv = 
        //         <div class="posts-installDiv-parent flexC" onClick = { ()=> window.open("https://play.google.com/store/apps/details?id=news.circle.circle&referrer=utm_source%3Damp_k%26utm_medium%3Dbutton")}>
        //             <div class="posts-install-item1 flexC"><h3 class="MarginAuto posts-installDiv-css1">{ this.state.displayMessage || "Circle App"}</h3></div>
        //             <div class="posts-install-item2 flexC"><h3 class="backgroundColorRed MarginAuto posts-installDiv-css1">{this.state.installMsg1}<br/>{this.state.installMsg2}</h3></div>
        //         </div>;

        return ( 
            <div>
                <div class="flexC setTopSticky">
                    <Circle setHomePage = { ()=>this.goToHomePage() }/>
                    <PostsHamburger displayStateChoosing={() => this.setStateChoosing()}/>
                </div>
                <CustomisedPoster hr={ hr.includes(this.state.city) } state={{
                    timePageOpened: this.state.timePageOpened,
                    postNumber: this.state.postNumber,
                    lang: this.state.lang,
                    origin: 'post',
                    city: this.state.city,
                    cityId: this.state.city ? config.getId(this.state.city,false) : "", 
                }}/>
                {main}
                <CustomisedPoster city= { this.state.city }/>
                <div class="posts-space"/>
                { topPosts }
                <div class="posts-space"/>
                { relatedPosts }
                {/* { installDiv } */}
                { <CustomisedInstallDiv
                    city={ this.state.city } 
                    displayMessage={ this.state.displayMessage }
                    installMsg1 = { this.state.installMsg1 }
                    installMsg2 = {this.state.installMsg2 }
                    openInstallPage = { ()=> this.openInstallPage() } /> }
            </div>
        );
    }
}

export default Posts;


