let headingTitle = "Select your State";
let stateNames = ["उत्तर प्रदेश","राजस्थान","കേരളം","हरियाणा"];

function Rajya(props){

    let checkedStatus = [0,0,0,0];

    let lsStateIndex = stateNames.indexOf(localStorage.getItem('stateName'));
    // mark the state in local storage as checked in the dialog below
    checkedStatus[lsStateIndex] = 1;

    // console.log('checkseds stat ====,',checkedStatus);

    let setNewStateName = (stateName) => {
        let oldStateName = localStorage.getItem('stateName');

        if(oldStateName !== stateName)
        {
            localStorage.setItem('stateName',stateName);

            // if stateName is 'Kerala' => language is malayalam else hindi
            if(stateName === 'കേരളം')
                localStorage.setItem('lang', 'ml_IN');
            else
                localStorage.setItem('lang', 'hi_IN');

            // remove local Storage Trending Topic and Trending News if state changed from Kerala to Other OR Other to Kerala
            if(oldStateName === 'കേരളം' || stateName === 'കേരളം'){
                // console.log('*** REMOVING THE TRENDING NEWS');
                localStorage.removeItem('trendingTopic');
                localStorage.removeItem('trendingNews');

                // delete the cityName and corresponging news
                localStorage.removeItem('cityName'); localStorage.removeItem('cityNews');
                
                // reset the showTrending: true; SINCE KERALA INVOLVED
                props.toggleShowRajya(2);
            }
            else{
                // delete the cityName and corresponding news
                localStorage.removeItem('cityName'); localStorage.removeItem('cityNews');

                // city reset
                props.toggleShowRajya(1);
            }
        }
        else
            // nothing reset
            props.toggleShowRajya(0);
    }

    let jsxStateNames = stateNames.map((stateName, index) => {
        return (
            <div class="font18 bold cont001 margin">
                <label onClick = {()=> setNewStateName(stateName)}>
                <input type="radio"
                        checked={ checkedStatus[index]}
                        name="radio"/>
                        {/* onClick = {()=> setNewStateName(stateName)}/> */}
                {stateName}
                <br/>
                </label>
            </div>
        );
    });

    let jsxHeading = <h1 class="margin padding font18">{headingTitle}</h1>;

    return (
        <div class="parent-container colorWhite opacity90">
            {jsxHeading}
            {jsxStateNames}
        </div>
    );
}

export default Rajya;


