import preact, {h} from "preact";
import { route } from 'preact-router';

import Router from 'preact-router';

import Dummy from './Dummy';
import City from './city';
import Home from './home';

let config = require("../Config");

export default function RouterMain(props){
    let stateName = localStorage.getItem('stateName');
    let cityName = localStorage.getItem('cityName')
    let trending = localStorage.getItem('trendingTopic');

    if(trending === null)
        trending = 'Trending';
    else{
        let lang = localStorage.getItem('lang');

        // set the trending text to be displayed in the native language using the land stored in local storage
        trending = config.trendingTopics.getTopic(trending,lang);
    }
    if(!cityName)
        cityName = 'My City';


    let childClass = "RouterMain-text";
    let containerChildClass = "RouterMain-active";
    let containerClass = "RouterMainDivChild";
    let activeContainerClass = "RouterMainDivChildActive";

    let handleRouterUrl = (url) => {
        // handling extended url
        let index = '';
        if(url.indexOf('/shehar') === 0)
            index = 0;
        else if(url.indexOf('/trending') === 0)
            index = 2;
        else if(url.indexOf('/') === 0)
            index = 1;
        
        // url change
        if(props.url !== url)
            props.updateIndex(index);
    }

    return (
        <preact.Fragment>
            <div class="RouterMain">
                <div class={ props.active[0] ? activeContainerClass : containerClass }>
                    <h1 class={ props.active[0] ? containerChildClass : childClass } onClick = { () => route("/shehar") }>{ cityName }</h1>
                </div>
                <div class={ props.active[1] ? activeContainerClass : containerClass }>
                    <h1 class={ props.active[1] ? containerChildClass : childClass } onClick={ () => route('/') }>{ stateName }</h1>
                </div>
                <div class={ props.active[2] ? activeContainerClass : containerClass }>
                    <h1 class={ props.active[2] ? containerChildClass : childClass } onClick={ () => route('/trending') }>{ trending }</h1>
                </div>
            </div>
            <Router onChange={(e)=>handleRouterUrl(e.url)}>
                <Home path="/" />
                <City path='/shehar' />
                <Dummy path="/trending"/>
            </Router>
        </preact.Fragment>
    );
}


