const getLanguagePerStateName = require("../functional-Javascript/getLanguagePerStateName");

export default function ShowStateSelection(props){
    let stateNames = ["उत्तर प्रदेश","राजस्थान","കേരളം","हरियाणा"];
    let headingTitle = "Select your State";
    
    let setStateName = (stateName) => {
        localStorage.setItem('stateName',stateName);

        // remove corresponding cityName and cityNews if existing
        localStorage.removeItem('cityName');
        localStorage.removeItem('cityNews');
        
        // set language in localStorage
        let language = getLanguagePerStateName(stateName);
        localStorage.setItem('lang',language);
        
        // since we are not closing this but rather setting up the new state
        props.onClose(false);
    }

    let jsxStateNames = stateNames.map( item => 
            <div class="font18 bold cont001 margin"
                onClick = {() => setStateName(item)}>
                {item}
            </div>);
    

    let jsxHeading = <h1 class="margin padding font18">{headingTitle}</h1>;

    return (
        <div class="parent-container colorWhite opacity90 positionRelative">
            <div class="postsCrossButton" onClick={()=>props.onClose(true)}>X</div>
            {jsxHeading}
            {jsxStateNames}
        </div>
    );
}


