import img from "../assets/circle-logo.svg";

// USE: 50% WIDTH IN FLEX
export default function Circle(){
    return(
        <div class="circle-container">
            <div class="circle-image-container">
                <img class="circle-image MarginAuto" alt="image" src={img}/>            </div>
            <div class="circle-title-container">
                <h2 class="circle-title">circle</h2>  
            </div>        
        </div>
    );
}


