const store = require('../Config');

let cities = {
    "उत्तर प्रदेश": store[store.name2codes["उत्तर प्रदेश"]],
    "राजस्थान": store[store.name2codes["राजस्थान"]],
    "കേരളം": store[store.name2codes["കേരളം"]],
    "हरियाणा": store[store.name2codes["हरियाणा"]],
}

export default function Shehr(props){
    let stateName = localStorage.getItem('stateName');
    let jsxCities = cities[stateName].map(item =>
            (<h3 class="shehr-city" onClick={()=>props.toggleCitySelection(item)}>{item}</h3>)
    );
        
    return (
        <div class="ful black">
            <div class="rel vh15 z-index">
                <h3 class="shehr-cross-button" onClick={()=> props.toggleCitySelection()}>X</h3>
                <h2 class="shehr-title">Select your City</h2>
            </div>
            <div class="rel vh85">
                <div class="scroll">
                    { jsxCities }
                </div>
            </div>
        </div>
    );
}


