let nativeText = {
    state: {
        hi_IN: "अपना राज्य बदलें",
        ml_IN: "സ്റ്റേറ്റ് മാറ്റൂ",
    },
    city: {
        hi_IN: "अपना शहर बदलें",
        ml_IN: "ജില്ല മാറ്റൂ",
    }
}

function ChangeSection(props){

    let change = props.change;
    let text = nativeText[change][localStorage.getItem('lang')];

    return (
        <div class="changeSelection-container" onClick={()=>change ==="state" ? props.toggleShowRajya() : props.toggleShowCitySelection()}>
            <p class="changeSelection-text">{text}</p>
        </div>
    );
}


// props.change = 'state' OR 'city' OR 'trending'
export default function ChangeSelection(props){
    let caseArray = ['state','city'];
    if(caseArray.includes(props.change))
        return <ChangeSection change={props.change}
                toggleShowRajya = {props.toggleShowRajya}
                toggleShowCitySelection = {props.toggleShowCitySelection}
                />;
    else
        return null;
}


