/* OLD WORKING FEED */
// '/feeds/nvstoriesnv/response?filter={"filterby.value":{"$in":["' + cityId + '"]}}',

// URLs for API call
const generateUrl = {
    city: (cityId, page) => prod + '/feeds/new_web_feed/response?filter={"filterby.value":{"$in":["' + cityId + '"]}}&page=' + page,
    state: (stateId, page) => prod + '/feeds/new_web_feed/response?filter={"filterby.value":{"$in":["' + stateId + '"]}}&page=' + page,
    trending: (topic, page, lang='hi_IN') => {
        let feedName;
        
        // if hindi speaking states
        if(lang === 'hi_IN')
            switch(topic){
                case 'national': feedName = 'national_v626'; break;
                case 'entertainment': feedName = "entertainment_v626"; break;
                case 'sports': feedName = "ipl_v626"; break;
                case 'special': feedName = "special_v626"; break;
                case 'farming': feedName = "krishi_v626"; break;
                case 'jobs': feedName = "jobs_v626"; break;
                case 'religious': feedName = "dharmik_v626"; break;
                case 'horoscope': feedName = "horoscope_v626"; break;
                case 'health': feedName = "fitness_v626"; break;
                case 'finance': feedName = "finance_v626"; break;
                case 'tech': feedName = "tech_v626"; break;
            }
        else if(lang === 'ml_IN')
            switch(topic){
                case 'national': feedName = 'national_v626_ml'; break;
                case 'entertainment': feedName = "entertainment_v626_ml"; break;
                case 'jobs': feedName = "jobs_ml"; break;
                case 'health': feedName = "health_v626_ml"; break;
                case 'tech': feedName = "technology_v626_ml"; break;
            }

        // console.log('^^^^^^^^^^^^^^^GENERATING URL',lang);

        let returnURL  = prod + "/feeds/" + feedName + "/response?page=" + page;

        // console.log('^^^^^^^^^^^^^^^^^ URL REQUEST IS',returnURL);

        return returnURL;
    }
}

// BELOW GIVEN FUNCTION IS NOT TO BE MODIFIED
// below given commented as browser was showing error for them
function generateResponse(url){
    return axios.get( url, {
        headers: {
            'Content-Type': 'application/json',
            "device-id": "web",
            "app-version": "2.6.04",
            "api-version": "0.0.1",
            "platform": "android",
            'Authorization': accessToken,

            // "api-version_number": '0.0.1',
            // "app-version_number": "40020696",
        },
    });
}

// CLEVERTAP API CALLING
function tpis(payload){
    return Promise.resolve(fetch('https://api.circle.news/tpis',{
                            method: 'POST',
                            headers: {
                                'Content-Type': 'application/json',
                                "device-id": "web",
                                "app-version": "2.6.04",
                                "api-version": "0.0.1",
                                "platform": "android",
                                'Authorization': accessToken,
                            },
                            body: JSON.stringify(payload),
                        })).then(data=>{
                            // localStorage.setItem('DATA',data);
                        });
}


let apis = {
    
    // ====> KEYS TO BE USED WHILE FETCHING DATA =====
    city: (cityId, page = 1) => generateResponse(generateUrl.city(cityId, page)),
    state: (stateId, page = 1) => generateResponse(generateUrl.state(stateId, page)),
    trending: (topic, page = 1,lang) => generateResponse(generateUrl.trending(topic, page, lang)),
    // ===== KEYS TO BE USED WHILE FETCHING DATA <====

    // ====> KEYS TO BE USED FOR CLEVERTAP API =====

    clevertap: (payload)  => (function(){
                            if(payload.identity)
                                tpis(payload);
                            else{
                                let id = setInterval(()=>{
                                    payload.identity = localStorage.getItem('fingerprint');
                                    if(payload.identity){
                                        tpis(payload);
                                        window.ga('send',{
                                                hitType: 'event',
                                                payload,
                                        });
                                        clearInterval(id);
                                    }
                                },2500);
                            }
                    })(),

    // ===== KEYS TO BE USED FOR CLEVERTAP API <====

    // ====> TESTING url for postman =====
    url: {
        city: (cityId) => generateUrl.city(cityId),
        state: generateUrl.state,
    },
    // ===== TESTING url for postman <====

    // original way below, SIMPLIFIED way above
    apiCallGet: (url, params) => {
                // console.log(params, "params from config js");
                return axios.get(prod + url, {
                    headers: {
                        'Content-Type': 'application/json',
                        "device-id": "web",
                        "app-version": "2.6.04",
                        "api-version": "0.0.1",
                        "platform": "android",
                        "api-version_number": 1,
                        "app-version_number": "40020604",
                        'Authorization': accessToken,
                        },
                    params: params,
                    });
                },
};

module.exports = apis;


