import preact from 'preact';
import Router, {route} from "preact-router";

import Home from './components/home';
import Landing from './components/landing';
import Posts from './components/posts';
import Agra from "./components/agra";

import "./index.css";

// FOR RECORDING CLEVERTAP EVENTS
const clevertap = require("./Apis").clevertap;

const getFingerprint = require("./getFingerprint");
const getLanguagePerStateName = require("./functional-Javascript/getLanguagePerStateName");

class Base extends preact.Component{
    constructor(){
        super();
        this.state = {
            stateName : '',
            componentNumber: 1, // 1 default 2 for /post
            
            // if componentNumber is 2 then postNumber has the postNumber
            postNumber: "",
            defaultState: 1,
        }
        this.clevertapEvent_WEB_OPEN_SOURCE(window.location.pathname);
    }

    // CLEVERTAP EVENT = WEB_OPEN_SOURCE
    clevertapEvent_WEB_OPEN_SOURCE(requestUrl){
        clevertap({
            eventName: 'WEB_OPEN_SOURCE',
            identity: localStorage.getItem('fingerprint'),
            eventData: {
                requestUrl,
                eventTime: new Date().getTime(),
            }
        });
    }

    componentDidMount(){
        // GENERATE THE FINGERPRINT AND STORE THE MURMUR HASH IN localStorage in key "fingerprint"
        //      if localStorage does not have the key 'fingerprint'
        if(!localStorage.getItem('fingerprint')){
            // console.log('generating the fingerprint');
            getFingerprint(2);
        }

        this.handleUrl(window.location.pathname);
    }

    componentDidUpdate(){
    }

    handleUrl(pathname){
        // change state if pathname corresponds to post
        if(pathname.indexOf('/post/') === 0){
            this.extractPostNumber(pathname);
            if(this.state.componentNumber !== 2)
                this.setState({ componentNumber: 2 });
        }
        else
            if(this.state.componentNumber !== 1)
                this.setState({ componentNumber: 1 });
    }


    extractPostNumber(url){
        // console.log('in here');
        let postNumber = url.slice(6);
        this.setState({ postNumber });
        route('/post/'+postNumber);
    }

    updateStateName(stateName){
        let fingerprint = localStorage.getItem('fingerprint');
        
        localStorage.removeItem('stateName');
        localStorage.removeItem('stateNews');
        localStorage.removeItem('cityName');
        localStorage.removeItem('cityNews');
        
        if(fingerprint)
            localStorage.setItem('fingerprint',fingerprint);
        localStorage.setItem('stateName',stateName);

        // set language in localStorage
        let language = getLanguagePerStateName(stateName);
        localStorage.setItem('lang',language);

        this.setState({stateName: stateName});
    }

    // SET SHEHAR PAGE FROM POST PAGE
    setSheharPage(){
        this.setState({ componentNumber: 1, defaultState: 0 });
    }

    // SET HOME PAGE FROM POST PAGE
    setHomePage(){
        this.setState({
            componentNumber: 1, defaultState: 1
        });
        route('/');
    }

    render(){
        if(window.location.pathname === '/shehar/agra'){
            localStorage.setItem('stateName',"उत्तर प्रदेश");
            localStorage.setItem('cityName',"आगरा");
            // window.location.pathname = '/shehar';
            return <Agra />
        }

        if(this.state.componentNumber === 1) {
            let stateName = localStorage.getItem('stateName');

            if(stateName){
                return <Home defaultState = {this.state.defaultState}/>;
            }
            else{
                return <Landing updateStateName={ (stateName)=> this.updateStateName(stateName) }
                />;
            }
        }
        else if(this.state.componentNumber === 2){
            
            let pathname = window.location.pathname;
            let postNumber = pathname.slice(6);

            return (
                <Router>
                    <Posts
                        path={`/post/:${this.state.postNumber}`}
                        postNumber={ postNumber }
                        changePost={ (url) => this.extractPostNumber(url)}
                        setSheharPage = { () => this.setSheharPage() }
                        setHomePage = { () => this.setHomePage() }
                        />
                </Router>
            );
        }
    }
}

export default Base;


