import Home from "./home";
import Shehr from "./shehr";

function City(){
    let cityName = localStorage.getItem('cityName');
    let stateName = localStorage.getItem('stateName');
    if(!cityName)
        <Shehr stateName = {stateName}/>
    else
        return (
            <Home path="/shehar"/>
        );
}

export default City;


