var config = require('./config');
let _ = require('lodash');

/* GET users listing. */
const responseData = function(number) {
  let data = {};
  let stateName = 'heheheheh';
  let finalData = {};
  const href = config.basePath + "/post/" + number;
  {
      const localities = config.localities();
      
      return Promise.resolve(config.apiCallGet('/feeds/webstories/response', { number: number }))
      
      .then(data=>{
        let message1='circle: लोकल न्यूज़ और वीडियो';
        let message11='अपने शहर का अपना ऐप';
        let message2 ='circle: പ്രാദേശിക വാർത്തകളും വീഡിയോകളും';
        let message3='circle: Local News & Videos';
        let installMsg1='Install';
        let installMsg2 = 'App';
        let restPost = [];
        let tagStories = [];
        let metaTitle = '';
        let keywords = [];
        let keywordsFinal = '';
        let topAd = 'https://d3viohk6mc296s.cloudfront.net/webAds/CWC-320x501.mp4';
        let bottomAd = 'https://d3viohk6mc296s.cloudfront.net/webAds/Video-300x250+(1).mp4';
        let image = false;
        if(data.data.data && data.data.data.length > 0){
          for(let i = 0; i < data.data.data.length; i++){
            if(i >= 1 && i < 6){
              restPost.push(data.data.data[i]);
            }
            if(i >= 6 && i < 15){
              tagStories.push(data.data.data[i]);
            }
          }
        }
        let post = data.data.data[0];
        if (post.data && post.data.engTitleKeyWords && post.data.engTitleKeyWords.length > 0) {
          for(let k of post.data.engTitleKeyWords) {
            keywords.push(k);
          }
        }

        if (post.data && post.data.engDescKeyWords && post.data.engDescKeyWords.length > 0) {
          for(let k of post.data.engDescKeyWords) {
            keywords.push(k);
          }
        }
        

        if (keywords.length > 0) {
          for(let i=0; i < keywords.length; i++) {
            if(i !== 0) {
              keywordsFinal = keywordsFinal + `, ${keywords[i]}`;
            } else {
              keywordsFinal = keywordsFinal + keywords[i];
            }
          }
        }
        let mainImage = '/images/icons/android-icon-512x512.png';
        for(var i=0; i < post.data.contents[0].media.length; i++){
          if(post.data.contents[0].media[i].type === "generic"){
            mainImage = post.data.contents[0].media[i].imgUrl;
            break;
          }
        }
        let profileName = post? post.data.profile.name.first + " " + post.data.profile.name.last : "सर्कल ऐप";
        const keys = config.getKeys(post.data.locality);
        if(keys.cityEng == undefined) {
          if(post && post.data && post.data.title != ''){
            metaTitle = `${post.data.title}`
          } else if (post && post.data && post.data.meta && post.data.meta.title) {
            metaTitle = `${post.data.meta.title}`
          }
        } else {
          const key = keys.cityEng;
          let res = key.split('-').pop();
            let cityEng = res.charAt(0).toUpperCase() + res.slice(1);
          if(post && post.data && post.data.title != ''){
            metaTitle = `${cityEng} News: ${post.data.title}`
          } else if (post && post.data && post.data.meta && post.data.meta.title) {
            metaTitle = `${cityEng}: ${post.data.meta.title}`
          }
        }
        
        // BEGIN ==> FOR NOTIFICATION
        let city = "", language = "";
        if(keys.cityEng){
          let string = keys.cityEng;
          city = string;
          // city = string.slice(string.indexOf('-')+1);
          language = (string.slice(0,string.indexOf('-')) !== 'kerala')? 'hi_IN' : 'ml_IN';
        }
        else{
          city = 'null', language = 'null';
         }
        // END ==> FOR NOTIFICATION

        if(keys.cityEng == undefined){
          let ortitle = keys.cityEng;
          ortitle='';
          keys.cityEng = '';
          finalData = {...finalData,ortitle,profileName,mainImage,keys, metaTitle, profile: post.data.profile, post,tagStories, keywordsFinal, restPost, basePath: config.basePath, localities, message1, message11, message2, message3, installMsg1, installMsg2, topAd, bottomAd, image,
            city, language};
        }
        else{
          installMsg1 = keys.translations.read_in_app.split('<br>')[0];
          installMsg2 = keys.translations.read_in_app.split('<br>')[1];
          for(let locality of localities) {
            for(let city of locality.cities){
              if(city.filter === keys.cityEng) {
                message1 = city.message1.split('<br>')[0];
                message11 = city.message1.split('<br>')[1];
                message2 = city.message2;
                message3 = city.message3;
              }
            }
          }
          let ortitle = keys.cityEng;
          const key = keys.cityEng;
          // console.log(key.toUpperCase())
          if(key.toUpperCase().indexOf('KERALA') > -1) {
            image = true;
            topAd = 'https://d3viohk6mc296s.cloudfront.net/webAds/CWC-Kerala-320x100.jpg';
            bottomAd = 'https://d3viohk6mc296s.cloudfront.net/webAds/CWC-Kerala-300x250.jpg';
          }
          // if(key.toUpperCase().indexOf('RAJASTHAN') > -1) {
            //   bottomAd = 'https://d3viohk6mc296s.cloudfront.net/webAds/CWC-Kerala-300x250.jpg';
            // }
            if(key.toUpperCase().indexOf('UTTAR_PRADESH') > -1) {
              bottomAd = 'https://d3viohk6mc296s.cloudfront.net/webAds/UP-300x250+(1).mp4';
            }
            let res = key.split('-').pop();
            let cityEng = res.charAt(0).toUpperCase() + res.slice(1);
            keys.cityEng = cityEng;
            
            /* SETTING UP HOME BUTTON TO UNIVERSAL OR KERALA DEPENDING UPON STATE NAME EXTRACTED FROM URL */
            // 'key' is <string> eg. 'rajasthan-alwar'
            stateName = key.toLowerCase().split('-')[0];
          // if(!stateName)
          //   console.log('NOT DEFINED');

        finalData = {...finalData,ortitle,profileName,mainImage,keys, metaTitle, profile: post.data.profile, post, tagStories, keywordsFinal, restPost, basePath: config.basePath, localities, message1, message11, message2, message3,installMsg1, installMsg2, topAd, bottomAd, image,
          // FOR HOME URL DIFFERENTIATION BASED ON STATE NAME 'stateName' IS USED
          stateName, city, language}
        }

        return finalData;

      },err => console.log(err))
      
      .then(finalData => {

      if(!finalData.stateName)
        finalData.stateName = null;

      let topPosts = [];
      let relatedPosts = [];

      
      // generate Top Posts & Related Posts
      let restPost = finalData.restPost;
      let tagStories = finalData.tagStories;
      let fn = (array, respondFor)=>{
        for(let i=0; i != array.length; ++i)
          /*  NO REPITITION OF POSTS
              typeof number = String AND typeof array[i].data.number = Number   */
          if(array[i].data.number != number)
            for(let j=0; j < array[i].data.contents[0].media.length; j++) {
                if(array[i].data.contents[0].media[j].type==="generic"){
                  if(respondFor === 'topPosts'){
                      // console.log('IN TOPPOSTS');
                      topPosts.push({
                          openUrl: "/post/" + array[i].data.number,
                          thumbnailUrl: "" + array[i].data.contents[0].media[j].imgUrl,
                          title: "" + array[i].data.title,
                      });
                    }
                  else if(respondFor === 'relatedPosts')
                      relatedPosts.push({
                        openUrl: "/post/" + array[i].data.number,
                        thumbnailUrl: "" + array[i].data.contents[0].media[j].imgUrl,
                        title: "" + array[i].data.title,
                    });
                    // BREAK USED SINCE MULTIPLE SWIPABLE PICS AS MULTIPLE GENERIC BUT WE WANT ONE
                    break;
                }
            }
      };


      fn(restPost, 'topPosts');
      fn(tagStories, 'relatedPosts');


      // handling of image url or video
      let post = finalData.post, type, url;
      if(post.data.layout == 'video' || post.data.layout == 'videoStory' ){
        for(var i=0; i < post.data.contents[0].media.length; i++){
          if(post.data.contents[0].media[i].type==="generic" ){
            if(post.data.contents[0].media[i].hlsPlaylistUrl && post.data.contents[0].media[i].hlsPlaylistUrl.length> 1){
              type = 'm3u8', url = post.data.contents[0].media[i].hlsPlaylistUrl;
            }
            else
            if(post.data.contents[0].media[i].videoUrl.length> 1){
              type = 'video', url = post.data.contents[0].media[i].videoUrl;
            }
          }
        }
      }
      else{
        for(var i=0; i < post.data.contents[0].media.length; i++) {
          if(post.data.contents[0].media[i].type==="generic" ){
            if(post.data.contents[0].media[i].videoUrl.length > 1){}else
              type="image"; url = post.data.contents[0].media[i].imgUrl;
          }
        }
      }

        let obj = {
          date: _.get(finalData, 'post.data.time.display', ""),
          city: _.get(finalData, 'keys.city', ""),
          headline: _.get(finalData, 'post.data.title', ""),
          reporter: _.get(finalData,'profileName', ""),
          news: _.get(finalData, 'post.data.description', ""),
          topPosts,
          relatedPosts,
          type,
          url,
          reportedId: _.get(finalData,['profile','_id'],""),
          reporterName: _.get(finalData,'profile.name',""),
          installMsg1: _.get(finalData, 'installMsg1', ""),
          installMsg2: _.get(finalData, 'installMsg2', ""),
          message1: _.get(finalData, 'message1', ""),
          message11: _.get(finalData, 'message11', ""),
          message2: _.get(finalData, 'message2', ""),
          message3: _.get(finalData, 'message3', ""),
          lang: _.get(finalData, 'language', ""),
        };

      // console.log('THE DATA IS AS',obj);

      return obj;
      });
  }
};

module.exports = responseData;



