const Fingerprint2 = require("fingerprintjs2");

// time is in mircoseconds
function getOurResult(time){
    return new Promise(resolve => {
        setTimeout(()=>{
            let options = {};
            Fingerprint2.get(options, function(components){
            let values =  components.map(function (component) { return component.value });

            // RETURN MURMUR HASH
            let murmur = Fingerprint2.x64hash128(values.join(''), 31);
            resolve(murmur);
            });
        },time);
    });
}

// defer the generation of fingerprint to 2 seconds by default
function generateFingerPrint(time_in_seconds = 2){
    getOurResult(time_in_seconds * 1000).then(value=>{
        localStorage.setItem('fingerprint',value);
    });
}

module.exports = generateFingerPrint;


