import preact from 'preact';
import thumbnail from "../assets/thumbnail.jpg";

function NewsThumbnail(props){
    if(props)
        return (
                <img class="news-thumbnail" alt="image" src={props.url}/>
        );
    else
        return (
                <img class="news-thumbnail" alt="image" src={ thumbnail }/>
        )
}

function NewsContent(props){
    
    // for testing only
    if(!props.data)
        return (
            <div class="news-content-container">
                <div class="news-reporter-container">
                    <p class="news-reporter">Gopal Prasad</p>
                </div>
                <div class="news-heading-container">
                    <p class="news-heading">
                        Cirle News ke liye testing. So just a template nothing more
                        out of it
                    </p>
                </div>
                <div class="news-details-container">
                    <p class="news-details-para"><span class="news-details-1st">{'फरह'}</span><span class="news-details-2nd"> • {'3'} घंटे पहले</span></p>
                </div>
            </div>
        );
    
    // for production
    else
        return (
            <div class="news-content-container">
                <div class="news-reporter-container">
                    <p class="news-reporter">{props.data.profile}</p>
                </div>
                <div class="news-heading-container">
                    <p class="news-heading">{props.data.title}</p>
                </div>
                <div class="news-details-container">
                    <p class="news-details-para"><span class="news-details-1st">{props.data.location}</span><span class="news-details-2nd">{props.time}</span></p>
                </div>
            </div>
        );
}

export default class News extends preact.Component{
    
    calculateTime(epoch){
        let difference = new Date(new Date() - new Date(epoch));
        let day = difference.getUTCDate(), hours = difference.getUTCHours(), minutes = difference.getUTCMinutes();
        let lang = localStorage.getItem('lang');
        // console.log('difference is',day,hours,minutes);
        
        if( day > 2)
            return lang === 'hi_IN' ? ` • ${ day } दिन पहले` : ` • ${ day } ദിവസങ്ങൾ`;
        else{
            if(day === 1 ){
                if(hours)
                    return lang === 'hi_IN' ? ` • ${ hours } घंटे पहलेे` : ` • ${ hours } മണിക്കൂറുകൾ`;
                else{
                    if(minutes)
                        return lang === 'hi_IN' ? ` • ${ minutes } मिनट पहले` : ` • ${ minutes } മിനിട്ടുകൾ`;
                    else
                        return lang === 'hi_IN' ? " • कुछ सेकंड पहले" : " • സെക്കന്റുകൾ";
                }
            }
            else
                return lang === 'hi_IN' ? ` • ${ 24 + hours } घंटे पहलेे` : ` • ${ 24 + hours } മണിക്കൂറുകൾ`;                
        }
    }

    render(){
        // conditioned as in case of city news only
        if(this.props.data){            
            // calculate time
            let time = this.calculateTime(this.props.data.time);

            return (
                <div class="news-container" onClick={()=> window.location.pathname = `post/${this.props.data.postUrl}`}>
                    <NewsThumbnail url={this.props.data.thumbnailUrlAndType.url}/>
                    {/* <NewsThumbnail/> */}
                    <NewsContent data={this.props.data} time={time}/> 
                </div>
            )
        }
        // if not city news then no props would be there => default template choosen
        else
            return (
                <div class="news-container">
                    <NewsThumbnail/>
                    <NewsContent />
                </div>
            )
    }
}


