import image from "../assets/india-gate.jpg";
import Circle from "./circle";

let stateNames = ["उत्तर प्रदेश","राजस्थान","കേരളം","हरियाणा"];

function Header(){
    return (
        <div class="header-container">
            <Circle/>
        </div>
    );
}

function Image(){
    return (
        <div class="landing-image-container">
            <img src={image} alt="india-gate" class="landing-image"/>
        </div>
    )
}

function States(props){
    let jsxStates = () => {
        return stateNames.map((stateName)=>{
                return (
                    <div class="landing-states" onClick={()=>props.updateStateName(stateName)}>
                        <h2 class="MarginAuto">{stateName}</h2>
                    </div>
                )
            }
        )
    }
    return (
        <div class="landing-states-container">
            { jsxStates() }
        </div>
    )
}

export default function Landing(props){
    return (
        <div class="vh100">
            <Header />
            <Image />
            <States updateStateName={props.updateStateName}/>
        </div>
    );
}


