// assuming that the timer id is in session storage and it's key is 'intervalId'
function clearIntervalId(){

    // clear interval
    let intervalId = Number(sessionStorage.getItem('intervalId'));
    clearInterval(intervalId);

    // clear corresponding items in sessionStorage
    sessionStorage.removeItem('intervalId');
    sessionStorage.removeItem('intervalState');
    sessionStorage.removeItem('intervalDuration');
    sessionStorage.removeItem('intervalVideoTime');
}

module.exports = clearIntervalId;


