import preact from 'preact';
import RouterMain from './routerMain';
import ChangeSelection from './changeSelection';
import Circle from './circle';
import News from "./news";
import Hamburger from './hamburger';
import Shehr from './shehr';
import Rajya from './rajya';
import Trending from './trending';
import {route} from 'preact-router';
import LoadMoreLoading from "./loadMoreLoading";

import img from '../assets/hamburger.png';
import Loading from './loading';

const _ = require('lodash');
// ==> fetching news
const apis = require('../Apis');
const config = require('../Config');
// fetching news <==

function Partition(){
    return <div class="partition flexC"></div>;
}

function Header(props){
    // return circle logo and title along with date and calendar
    return (
        <div class="header-container">
            <Circle/>
            <div class="calendarAndHamBurger-container">
                <div class="calendarAndHamBurger-child1">
                    {/* <h3 class="font14 MarginAuto">{ props.newsDate }</h3> */}
                </div>
                <div class="calendarAndHamBurger-child2">
                    <img class = "hamburger-image" src={img} alt="hamburger" onClick={()=>props.toggleHamburger()}/>
                </div>
            </div>
        </div>
    );
}

// LOAD MORE FIELD
function LoadMore(props){
    return (
        <div class="loadMore-container flexC jc-center">
            <div class="w30 flexC" onClick={()=>props.incrementPage()}>
                <p class="MarginAuto">LOAD MORE</p>
            </div>
        </div>
    );
}

function LocalFeed(props){
    // console.log('*** LOCAL FEED EXEC STARTED');
    let change;
    let section = props.section;
    switch(section){
        case 0: change = 'city'; break;
        case 1: change = 'state'; break;
        case 2: change = 'trending'; break;
    }

    let jsxNews = [];
    jsxNews.push(<ChangeSelection change={change}
            lang={"hi_IN"}
            toggleShowRajya={props.toggleShowRajya}
            toggleShowCitySelection={props.toggleShowCitySelection}
            />);
        
    // parse JSON news from localStorage
    let jsonOurData;
    switch(section){
        case 0: jsonOurData = localStorage.getItem('cityNews'); break;
        case 1: jsonOurData = localStorage.getItem('stateNews'); break;
        case 2: jsonOurData = localStorage.getItem('trendingNews'); break;
    }

    let ourData;
    if(jsonOurData)
        ourData = JSON.parse(jsonOurData);

    // console.log('IN NEWS PUSH SECTION',ourData);

    // console.log('indiv',ourData[0]);

    if(ourData)
    ourData.forEach(arrItem =>
        arrItem.forEach(obj => jsxNews.push(<News data={obj}/>, <Partition/>))
    );
    // ourData.forEach(obj => jsxNews.push(<News/>));
    
    /**
     *  if 
     *      props.showLoadMoreLoading = true => push the required animation
     *  else
     *      push the load more button
     *  */
    if(props.showLoadMoreLoading === true)
        // show loading
        jsxNews.push(<LoadMoreLoading/>);

    /* SINCE AUTO-RELOAD FUNCTIONALITY ADDED */
    // SO NO NEED OF BELOW GIVEN LOADMORE COMPONENT
        // else
        //     // button to load more
        //     jsxNews.push(<LoadMore incrementPage={props.incrementPage}/>);
    
    
    return (
        <div class="home-news-container">
            {jsxNews}
        </div>
    );
}

function LocalFixed(props){
    return (
        <div class="sticky">
            <Header toggleHamburger={props.toggleHamburger}
                newsDate={props.newsDate}
            />
            <RouterMain updateIndex={props.updateIndex}
                    updateTrending={props.refresh}
                    url = {props.url}
                    active = {props.active}
                    />
        </div>
    );
}

class Home extends preact.Component{
    constructor(){
        super();
        this.state = {
            /** displaySection: [x,y,z] corresponds as
             *  x = city, y = state, z = trending
             */
            displaySection: [0,0,0],
            showHamburger: false,
            showCitySelection: true,
            previousState: 1,
            refresh: true,
            showRajya:false,
            newsDate: Date().slice(4,10),
            // for Loading Screen
            showLoading: false,
            showTrending: true,

            // animation for load more loading
            showLoadMoreLoading: false,
        }
    }

    /* LIFECYCLE METHOD componentDidMount */
    componentDidMount(){
        let indexPerPathname = this.getIndexPerPathname();
        // console.log('defaultState is',this.props.defaultState);

        if(this.props.defaultState === 0)
            this.initState(0)
        else
            this.initState(indexPerPathname);
        this.setState({showLoading: true});

        // add event listener for scrolling to load more feeds
        this.autoLoadMoreFeeds();
    }

    /* LIFECYCLE METHOD componentDidUpdate */
    componentDidUpdate(undefined, prevState){
        // console.log('update', this.state);
        
        this.setRoutePerDisplaySection();
        
        let index = this.getChoice();

        if( (this.state.showLoading && !prevState.showLoading) || (this.state.showLoadMoreLoading && !prevState.showLoadMoreLoading) ){
            this.fetchNews(index);
        }
    }

    /* FUNCTION TO IMPLEMENT AUTORELOAD */
    autoLoadMoreFeeds(){
        
        // function to be passed in as argument for below given event listener
        let eventFunctionScroll = () => {
            let postHeight = Math.pow(2,7) - 1; // ninja value
            let clientHeight = document.documentElement.clientHeight;
            let scrollHeight = document.documentElement.scrollHeight;
            let scrollingAvailable = scrollHeight - clientHeight;

            // reload page when the scroll bar reaches the (posts - 2) from end (total 9 posts)
            if(scrollY > (scrollingAvailable - (postHeight * 2))){
                this.setState({ showLoadMoreLoading: true});
            }
        }
        
        // add event listener for SCROLLING
        window.addEventListener('scroll',eventFunctionScroll);
    }

    /* DELETE NEWS CORRESPONDING TO THE DISPLAY SECTION INDEX PASSED */
    deleteOldNews(index){
        switch(index){
            case 0: localStorage.removeItem('cityNews');break;
            case 1: localStorage.removeItem('stateNews');break;
            case 2: localStorage.removeItem('trendingNews');break;
        }
    }

    /* INITIALISE STATE BASED ON THE PATHNAME */
    initState(index){
        this.deleteOldNews(index);
        let displaySection = [0,0,0]; displaySection[index] = 1;
        if(localStorage.getItem('cityName'))
            this.setState({ showCitySelection: false });
        if(localStorage.getItem('trendingTopic'))
            this.setState({ showTrending: false, });
        this.setState({ displaySection });
    }

    // ==================> MARK VERIFY
    toggleShowTrending(cross, value=""){

        // i.e closed without choosing trending option
        if(cross){
            this.setDisplaySection(this.state.previousState);
        }
        else{   
                // compare with the old value in local storage
                let trendingTopic = localStorage.getItem('trendingTopic');
                if(trendingTopic === value){
                    this.setState({showTrending: false})
                }
                else{
                    // set the new value of trendingTopic in localStorage
                    localStorage.setItem('trendingTopic',value);
                    this.setState({showTrending: false, showLoading: true});
                }
            }
    }

    // check if the display section and the pathname in the url are same or not
    getIndexPerPathname(){
        let path = window.location.pathname;
        if(path.indexOf('/shehar') === 0)
            return 0;
        if(path.indexOf('/trending') === 0)
            return 2;
        
        // else the page asked is home page
        return 1;
    }

    // ==================> MARK DUPLICATE
    setPathnamePerState(){
            switch(this.getChoice()){
            case 0: route('/shehar');
            case 1: route('/');
            case 2: route('/trending');
        }
    }

    // returning the index of array containing a sigle value of '1'
    returnIndexOfTrueValue(arrayName){
        return arrayName.findIndex(item=> item);
    }

    /* CHANGE THE URL BASED ON DISPLAY SECTION */
    setRoutePerDisplaySection(){
        
        if(this.state.displaySection[0] && window.location.pathname !== '/shehar'){
            // console.log('setting route from ',window.location.pathname,'to /shehr');
            route('/shehar');
        }
        if(this.state.displaySection[1] && window.location.pathname !== '/'){
            // console.log('setting route from ',window.location.pathname,'to /');
            route('/');
        }
        if(this.state.displaySection[2] && window.location.pathname !== '/trending'){
            // console.log('setting route from ',window.location.pathname,'to /trending');
            route('/trending');
        }
    }

    getNameAndNews(index){
        switch(index){
            case 0: return {
                        name: 'cityName',
                        news: 'cityNews',
                    };
            case 1: return {
                        name: 'stateName',
                        news: 'stateNews',
                    };
            case 2: return {
                        name: 'trendingTopic',
                        news: 'trendingNews',
                    }
        }
    }

    fetchNews(choiceIndex){
        // console.log('IN FETCH NEWS FOR INDEX IS',choiceIndex);

        let localStorageName, id, promise;

        // check for old news in local storage and find page number according to that
        let lsName;
        switch(choiceIndex){
            case 0: lsName = 'cityNews'; break;
            case 1: lsName = 'stateNews'; break;
            case 2: lsName = 'trendingNews'; break;
        }

        // console.log('lsname is',lsName);

        let oldJSONFeed = localStorage.getItem(lsName);
        // console.log('OLD JSON FEED',oldJSONFeed);
        let parsedJSONFeed = JSON.parse(oldJSONFeed);
        let page = parsedJSONFeed ? parsedJSONFeed.length +1 : 1;
        
        // console.log("PAGE IS",page);
        
        let lang = localStorage.getItem('lang');
        
        // console.log("+++ LANG FOR REQUEST",lang);

        switch(choiceIndex){
            case 0: localStorageName = localStorage.getItem('cityName');
                    id = config.getId(localStorageName, false);                    
                    /* IF DIRECTLY /shehr OPENED, DON'T SEND ANY REQUEST FOR UNDEFINED DATA */
                    if(localStorageName)
                        promise = new Promise(resolve => resolve(apis.city(id, page)));
                    break;

            case 1: localStorageName = localStorage.getItem('stateName');
                    id = config.getId(localStorageName, true);
                    promise = new Promise(resolve => resolve(apis.state(id, page)));
                    break;

            case 2: localStorageName = localStorage.getItem('trendingTopic');
                    /* IF DIRECTLY /trending OPENED, DON'T SEND ANY REQUEST FOR UNDEFINED DATA */
                    if(localStorageName)
                        promise = new Promise(resolve => resolve(apis.trending(localStorageName, page, lang)));
                    break;
        }

        // console.log('+++ Request Processed');

        if(localStorageName !== null){
            // console.log('&&& into this');
            // response which we will be recieving
            let response;

            // modified response to be stored in localStorage

            let ourData = [];
            
            // console.log('OURDATA TO BE CHECKED',ourData);

            promise.then(result => {

                // console.log('REQUEST MAR DI HAI');

                if(result.status === 200 && result.data.data){
                    // console.log('*** executing the promise');
                    // console.log('*** viewing the local storage length',
                    //     JSON.parse(localStorage.getItem(lsName)).length);
                    // store array of data and process as required
                    response = result.data.data;

                    // console.log('processing of response');

                    // console.log('response is',response);

                    ourData = response.filter(element=>element.data.status === 'published').map(function(element){
                        
                        let obj = element.data;

                        // TESTING
                        // if(!index){
                        //     console.log('object is',obj,'\nlodash',_.get(obj,'title','hehe'),obj.title);
                        //     console.log('==============\n\n\n');
                        //     let temp = {
                        //         key1: 'val1',key2: 'val2',key3: { key31: 'val31'}
                        //     };
                        //     console.log(_.get(temp,'key3.key31','NONE'));
                        // }

                        let getProfile = (object) =>{
                            let first,second;
                            first = _.get(object,'profile.name.first',"").trim();
                            if(!first)
                                return ""
                            else{
                                second = _.get(object,'profile.name.last',"").trim();
                                if(!second)
                                    return first;
                                else
                                    return (first + " " + second);
                            }
                        };

                        let getThumbnailUrlAndType = (arr,obj) => {
                            // console.log('ARRRR',arr);
                            if(arr === null)
                            return {url: '',type: ''};
                            else{
                                let index = arr.findIndex(arrObj => arrObj.type === "generic" );
                                // console.log('999999999999999',index);
                                if(index === -1)
                                    return {url: '', type: ''};
                                let url = arr[index].imgUrl;
                                let videoUrl = arr[index].videoUrl;
                                if(videoUrl)
                                    return {url, type: 'video'};
                                else
                                    return {url, type: 'image'};
                            }
                        }

                        return ({
                            title: _.get(obj,'title',''),
                            time: _.get(obj,'time.epoch',0),
                            profile: getProfile(obj),
                            location: _.get(obj,'locality.thana',''),
                            // {url, type: 'image'/'video'}
                            thumbnailUrlAndType: getThumbnailUrlAndType(_.get(obj,'contents[0].media',null),obj),
                            postUrl: _.get(obj,`number`,""),
                        })
                    
                    });

                    // console.log('FETCHED NEWS IS AS OURDATA',ourData);
                    // console.log('*** display the cleanold state',this.state.cleanOld[choiceIndex]);
                    
                        // console.log('*** since the state = 1, exectuing the else part');
                    
                    let arr = [];

                    if(page !== 1)
                        arr = [...parsedJSONFeed];
                    
                    arr.push(ourData);
                    let ourJSONData = JSON.stringify(arr);
                    localStorage.setItem(lsName, ourJSONData);
                    
                    // used the facility for this once
                    // console.log('*** the set cleanold state',this.state.cleanOld[choiceIndex]);
                    // console.log('*** local stroage',JSON.parse(localStorage.getItem(lsName)));
                    

                    // console.log('*** DROPPING THE LOADING SCREEN');
                    // if(this.state.showLoadMoreLoading)
                        this.setState({showLoading: false, showLoadMoreLoading: false})
                    // else
                    //     this.setState({showLoading: false})
                }
            },
            reject => {
                // console.log('DATA NOT FETCHED, ERROR WHILE HANDLING API\n', reject);
                // if(this.state.showLoadMoreLoading)
                    this.setState({showLoading: false, showLoadMoreLoading: false});
                // else
                //     this.setState({showLoading: false});
            });
        }
        else{
            // console.log('here in this', this.state.showLoadMoreLoading);
            // if(this.state.showLoadMoreLoading)
                this.setState({showLoading: false, showLoadMoreLoading: false});
            // else
            //     this.setState({showLoading: false});
        }
    }

    toggleShowRajya(){
        this.setState({showRajya: !this.state.showRajya});
    }

    /* SET STATE AND NEWS ACCORDING TO THE SELECTED OPTION FROM THE HAMBURGER MENU */
    toggleHamburger(value, newTrendingTopic){

        switch(value){
            case 'Home': this.setState({displaySection: [0,1,0] }); break;
            case 'City': this.setState({displaySection: [1,0,0] }); break;
            default : break;
        }
         
        if(value === 'default'){
            // console.log('new value is',newTrendingTopic);
            // compare trending topic with the locally set trending topic name
            let oldTrendingTopic = localStorage.getItem('trendingTopic');
            if( newTrendingTopic !== oldTrendingTopic){
                // console.log('updating new');
                // removing corresponding news
                localStorage.removeItem('trendingNews');
                // console.log('CHECKING FOR REMOVED NEWS',localStorage.getItem('trendingNews'));
                // setting new topic for trendingTopic
                localStorage.setItem('trendingTopic',newTrendingTopic);
                // console.log('reading value from local storage',localStorage.getItem('trendingTopic'));
                // set displaySection accordingly
                this.setState({displaySection: [0,0,1], showLoading:true});
                // this.setState({displaySection: [0,0,1]});
            }
            this.setState({ showTrending: false});
        }

        this.setState({showHamburger: !this.state.showHamburger });
    }

    refresh(){
        this.setState({refresh: !this.state.refresh});
    }

    setDisplaySection(index){
        let nameAndNews = this.getNameAndNews(index);
        
        // if the section to be set does not have any news -> set the showLoading to true for fetching news for it
        if(!localStorage.getItem(nameAndNews.news))
            this.setState({ showLoading: true });

        let previousState = this.getChoice();
        let displaySection  = [0,0,0];
        displaySection[index] = 1;
        this.setState({displaySection, previousState});
    }

    // ==================> MARK SIMPLIFY
    getChoice(){
        let arr = this.state.displaySection;
        for(let i=0;i!=arr.length;++i){
            if(arr[i]){
                return i;
            }
        }
    }

    // if user wishes to change city
    toggleShowCitySelection(){
        this.setState({showCitySelection: !this.state.showCitySelection });
    }

    toggleCitySelection(cityName){
        if(cityName === undefined){
            // if cityName already selected => hide the city selection menu
            if(localStorage.getItem('cityName')){
                this.setState({ showCitySelection: false });
            }
            else{
                // ==================> MARK link to setDisplaySection for setting up the the BOUNDARY CASE BELOW
                let displaySection = [0, 0, 0];
                displaySection[this.state.previousState] = 1;

                /* BOUNDARY CASE */
                // if stateNews not present i.e. user presses X button after opening pathname /shehar
                if(!localStorage.getItem('stateNews'))
                    this.setState({ showCitySelection: true, displaySection, showLoading: true });
                // stateNews is present => show state directly but don't reload
                else
                    this.setState({ showCitySelection: true, displaySection });
            }
        }
        // if cityName argument is present => 
        else{
            localStorage.setItem('cityName',cityName);
            localStorage.removeItem('cityNews');
            this.setState({showCitySelection: false, showLoading: true});
        }
    }

    toggleLocalStorageValues(value){
        // stateName changed => city choosing option to be shown again
        if(value === 1){
            localStorage.removeItem('stateNews');
            this.setState({showRajya: false, showCitySelection: true, showLoading: true });
        }
        // kerala is involved
        else if(value === 2){
            localStorage.removeItem('stateNews');
            this.setState({showRajya: false, showCitySelection: true, showLoading: true, showTrending: true });
        }
        
        // same state selected
        else
            this.setState({showRajya: false});
    }

    incrementPage(){
        this.setState({ showLoadMoreLoading: true});
        // this.setState({showLoading: true});
    }

    render(){

        if(this.state.showLoading)
            return <Loading/>
        if(this.state.displaySection[2] && this.state.showTrending)
            return <Trending toggleShowTrending={(cross,value) => this.toggleShowTrending(cross,value)}/>
        if(this.state.showRajya)
            return <Rajya toggleShowRajya = {(value) => this.toggleLocalStorageValues(value)}/>

        if(this.state.displaySection[0] && this.state.showCitySelection){
            return <Shehr toggleCitySelection={(city)=>this.toggleCitySelection(city)}/>
        }

        if(this.state.showHamburger)
            return <Hamburger toggleHamburger={(value, trendingTopic)=>this.toggleHamburger(value, trendingTopic)}/>;
         // if city is being displayed

        
        else{
            return (
                <div class="home-container">
                    <LocalFixed updateIndex={(i)=> this.setDisplaySection(i)}
                            toggleHamburger={()=>this.toggleHamburger()}
                            refresh={()=>this.refresh()}
                            url = {window.location.pathname}
                            active = {this.state.displaySection}
                            newsDate = {this.state.newsDate}
                            />
                    <LocalFeed section={this.getChoice()}
                            toggleShowRajya={()=>this.toggleShowRajya()}
                            toggleShowCitySelection={()=>this.toggleShowCitySelection()}
                            incrementPage={()=>this.incrementPage()}
                            showLoadMoreLoading = {this.state.showLoadMoreLoading}
                    />
                </div>
            );
        }
    }
}

export default Home;


