const trendingTopics = require('../Config').trendingTopics;

import finance from "../assets/trending/finance.png";
import bhakti from "../assets/trending/bhakti.png";
import jobs from "../assets/trending/jobs.png";
import khel from "../assets/trending/khel.png";
import krishi from "../assets/trending/krishi.png";
import manoranjan from "../assets/trending/manoranjan.png";
import rashifal from "../assets/trending/rashifal.png";
import rashtriya from "../assets/trending/rashtriya.png";
import sehat from "../assets/trending/sehat.png";
import tech from "../assets/trending/tech.png";
import vishesh from "../assets/trending/vishesh.png";

let trendingThumbnails = {
    hi_IN: [
        rashtriya, manoranjan, khel, vishesh, krishi, jobs, bhakti, rashifal,
        sehat, finance, tech
        ],
    ml_IN: [
        rashtriya, manoranjan, jobs, sehat, tech
    ],
};


function Topics(props){
    let lang = localStorage.getItem('lang');
    let jsxTopics = trendingTopics[lang].map((item, index)=>{
        return (
            <div class="trending-topics-items" onClick={()=> props.toggleShowTrending(false,item.value)}>
                <div class="trending-topics-icon-container">
                    <img class="trending-topics-icon" src={ trendingThumbnails[lang][index] }/>
                </div>
                <div class="trending-topics-text-container">
                    <h3 class="trending-topics-text">{item.topic}</h3>
                </div>
            </div>
        );
    });

    return (
        <div class="trending-topics-container">
            { jsxTopics }
        </div>
    )
}

export default function Trending(props){
    let title = "Select the Topics";
    return (
        <div class="trending-container">
            <h2 class="Cross-Button" onClick={()=> props.toggleShowTrending(true)}>x</h2>
            <h2 class="trending-title">{title}</h2>
            <Topics toggleShowTrending={props.toggleShowTrending}/>
        </div>
    );
}


